#container {
  margin: 25px;
  margin-top: 50px;
  color: #282828;
}
#hline {
  visibility: hidden;
}
#textColP {
  font-size: 40 !important;
}
#firstName,
#lastName {
  text-transform: capitalize;
}
#firstName:focus,
#lastName:focus {
  text-transform: none;
}
#guestCountError {
  color: #a94442;
}
.help-block {
  text-align: right;
}
#mainRow {
  color: #282828;
}

h1 {
  font-family: sans-serif;
  font: arial;
  font-weight: 500;
  color: #006400;
}
h2 {
  font-family: sans-serif;
  font-weight: 400;
  color: #006400;
  font-size: 20px;
}
button {
  border-radius: 5px !important;
  border: 1px solid #282828 !important;
  background-color: #ffffff !important;
  color: #282828 !important;
  margin-top: 10px;
  margin-left: 10px;
}

button:hover {
  -webkit-transform: scale(1.03);
  -ms-transform: scale(1.03);
  transform: scale(1.03);
}
.btn:focus,
.btn:active {
  outline: none !important;
  box-shadow: none;
}
.border {
  margin: 10px;
  padding: 10px;
}

body {
  margin: 1px;
  background-image: linear-gradient(24deg, #7aa2b3, #98ea7c);
}

@media (min-width: 768px) {
  #formCol {
    width: 45%;
    background-color: rgba(0, 0, 0, 0.1);
    padding: 15px;
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.75);
    border-radius: 5px;
  }

  #textColP p {
    font-size: 18px;
  }
  #textColP {
    width: 85%;
  }
}

@media (max-width: 760px) {
  #container {
    margin-top: 0px;
    margin-bottom: 50px;
  }
  #procoreLogo {
    width: 90%;
  }
  #formHeader {
    margin-bottom: 10px;
  }
  #textColP {
    margin-bottom: 50px;
  }
  .col-sm-6 {
    padding-right: 0px;
    padding-left: 0px;
  }
  #hline {
    visibility: initial;
    border-color: #282828;
  }
}
